<template>
  <div id="container">
    <div id="header">
      <home-header :nav="item"></home-header>
    </div>
    <div id="bodys">
      <transition-group :name="transitionName" tag="div" :class="'bodys-content'">
        <home-body-content
          :allImgData="allImgData"
          :key="contentName + index"
          v-for="(contentName, index) in item"
          v-show="showIndex === index"
        ></home-body-content>
      </transition-group>
    </div>
    <footer-navigator></footer-navigator>
  </div>
</template>

<script>
import HomeHeader from './components/Header'
import HomeBodyContent from './components/bodyContent'
import FooterNavigator from '@/common/Navigator/Navigator'
import eventBus from '@/eventBus.js'
import axios from 'axios'

export default {
  name: 'Home',
  components: {
    HomeHeader,
    HomeBodyContent,
    FooterNavigator
  },
  data () {
    return {
      item: ['', '', '', '', '', '', '', '', ''],
      allImgData: {},
      showIndex: 0,
      transitionName: 'left'
    }
  },
  methods: {
    getData (url) {
      axios.get(url)
        .then(this.process)
    },
    process (data) {
      this.allImgData = data.data
    }
  },
  mounted () {
    this.getData('./static/mock/img.json')
    eventBus.$on('setShowIndex', (index) => {
      if (index > this.showIndex) {
        this.transitionName = 'left'
      } else {
        this.transitionName = 'right'
      }
      this.showIndex = index
    })
  }
}
</script>

<style lang="less" scoped>
@import "~styles/common.less";

  #container {
    overflow: hidden;
    box-sizing: border-box;
    padding-top: 27.38rem / @baseFont;
    height: 100%;
  }
  #header {
    z-index: 10;
    position: absolute;
    top: 0;
    left: 0;
    height: 69.39rem / @baseFont;
  }
  #bodys {
    position: relative;
    overflow-x: hidden;
    overflow-y: auto;
    height: 100%;
  }
  .left-enter {
    left: 100%;
  }
  .left-enter-to, .right-enter-to{
    left: 0%;
  }
  .left-enter-active, .right-enter-active {
    transition: left .5s ease-in-out;
  }
  .left-leave, .right-leave {
    left: 0%;
  }
  .left-leave-to {
    left: -100%;
  }
  .left-leave-active, .right-leave-active {
    transition: left .3s ease-in-out;
  }
  .right-enter {
    left: -100%;
  }
  .right-leave-to {
    left: 100%;
  }
</style>



